import React,{useEffect} from 'react';
import {getWindowDimensions} from "src/components/supporting/UseWindowsDimensions";
import store from 'src/store';
import {updateTesting} from "src/actions/testingActions";


const UseWindowsDimensionsTest = () => {
    useEffect(()=>{
        store.dispatch(updateTesting({testDimensions:getWindowDimensions(),windowWidth:window.innerWidth,windowHeight:window.innerHeight}))
    },[]);
    return (
        <div>
            <div>

            </div>
        </div>
    )
};

export default UseWindowsDimensionsTest;
